.card-profile-wrapper .card-profile a.item-sosmed {

    &:hover {
        @apply bg-black;

        svg {
            &.svg-dribble path {
                @apply transition-all duration-300 fill-white;
            }

            &.svg-behance path {
                @apply transition-all duration-300 fill-white;
            }

            &.svg-instagram path {
                @apply transition-all duration-300 fill-white;
            }

            &.svg-linkedin path {
                @apply transition-all duration-300 fill-white;
            }
        }
    }
}


#digital-warrior .slide-item.slick-slide {
    @apply max-lg:px-2;
}



.digital-warriors {
    .slick-track{
        @apply flex items-center;
    }
    .slick-list {
        @apply overflow-x-hidden; 
        overflow-y: none;
        overflow: unset !important;
    }

    .slick-dots li{
        @apply w-fit h-fit mx-1;
        button {
            @apply transition-all duration-300 w-[24px] h-[2px] bg-[#0A2C9F]/20 rounded-[100px] p-0;
            &::before{
                @apply content-none hidden w-0 h-0;
            }
        }
        &.slick-active {
            button {
                @apply transition-all duration-300 bg-[#0A2C9F]/100;
            }
        }
        &:hover {
            button {
                @apply transition-all duration-300 bg-[#0A2C9F]/100;
            }
        }
    }

    .slick-dotted.slick-slider {
        @apply mb-0 lg:mb-10;
    }

    .slick-dots {
        @apply -bottom-[40%] lg:-bottom-[25%] transform -translate-y-1/2 lg:absolute relative pt-10 lg:pt-0
    }

    // ul.slick-dots li button {
    //     @apply p-0 w-[6px] h-[6px] text-[12px];

    //     &:before {
    //         color: #D3D4D8 !important;
    //     }
    // }

    // .slick-dots li.slick-active button:before {
    //     color: #0F39A6 !important;
    // }
}
@media only screen and (min-width: 1024px) {

    .digital-warriors {
        .slick-track {
            .slick-slide {
                @apply lg:flex transition-all duration-200 ease-linear;
    
                img {
                    @apply transition-all duration-500 scale-x-95;
                }
    
                &:hover {
                    @apply scale-[1.1] mx-[12px];
                    transition-timing-function: linear;
                    // width: 35vw !important;
    
                    img {
                        @apply transition-all delay-200 duration-200 ease-linear;
                    }
                }
            }
        }
    }
}